<template>
  <div class="panel panel-login">
    <el-form ref="loginForm" autoComplete="off" :model="loginForm" :rules="loginRules" label-position="left">
      <div class="logo mid-flex">
        <img src="../assets/images/logo.png" alt="">
        <h5>欢迎登录维信金科商户管理系统</h5>
      </div>
      <el-form-item prop="username">
        <el-input name="username" type="text" v-model="loginForm.username" maxlength="32" autoComplete="off" placeholder="请输入账户名">
          <img slot="prefix" src="../assets/images/icon-user.png" alt="">
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input name="password" type="password" v-model="loginForm.password" maxlength="16" autoComplete="off" placeholder="请输入账户密码">
          <img slot="prefix" src="../assets/images/icon-pass.png" alt="">
        </el-input>
      </el-form-item>
      <el-form-item prop="verificationCode">
        <el-input name="verificationCode" type="text" @keyup.enter.native="handleLogin" v-model="loginForm.verificationCode" maxlength="4" autoComplete="off" placeholder="请输入验证码">
          <img slot="prefix" src="../assets/images/icon-code.png" alt="">
          <span slot="suffix" class="captcha-btn" :class="{sending: isSending}" @click.stop="sendCode">{{codeText}}</span>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">登录</el-button>
      </el-form-item>
      <!-- <div class="tips">
        <router-link to="/login">忘记密码</router-link>
      </div> -->
    </el-form>
    <div class="copyright">
      <span>版权所有@上海维信荟智金融科技有限公司 沪ICP备11048686号-11</span>
    </div>
  </div>
</template>

<script>
import { getCaptcha } from '@/api/index.js';

export default {
  name: 'login',
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
        userpwd: '',
      },
      loginRules: {
        username: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur',
          },
          {
            min: 4,
            max: 12,
            message: '长度在 4 到 12 个字符',
            trigger: 'blur',
          },
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur',
          },
          {
            min: 6,
            max: 15,
            message: '长度在 6 到 15 个字符',
            trigger: 'blur',
          },
        ],
        verificationCode: [{
          required: true,
          message: '请输入验证码',
          trigger: 'blur',
        }],
      },
      loading: false,
      isSending: false,
      sendCount: 0,
      total: 60,
    };
  },
  computed: {
    codeText() {
      if (this.sendCount === 0) {
        return '获取验证码';
      }
      if (!this.isSending) {
        return '重新获取';
      }
      return `${this.total}s`;
    },
  },
  methods: {
    sendCode() {
      if (this.isSending) {
        return;
      }
      if (!this.loginForm.username) {
        this.$message.warning('请输入用户名');
        return;
      }
      this.isSending = true;
      this.sendCount += 1;
      this.startCountDown();
      getCaptcha(this.loginForm.username, '200110').finally(console.log);
    },
    startCountDown() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.total = 60;
      this.countDown();
    },
    countDown() {
      this.timer = setTimeout(() => {
        if (this.total > 0) {
          this.total -= 1;
          this.countDown();
        } else {
          this.isSending = false;
        }
      }, 1000);
    },
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.$store
            .dispatch('LOGIN', this.loginForm)
            .then(() => {
              this.$router.push({ path: '/' });
            })
            .catch(console.error);
        } else {
          console.log('error submit!!');
        }
      });
    },
  },
};
</script>

